import React, { useState, useEffect, useCallback } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';
import toast from 'react-hot-toast';
import {
  PlusIcon,
  ExclamationTriangleIcon,
  ClockIcon,
  HeartIcon,
  DocumentArrowDownIcon,
  CalendarDaysIcon,
  PharmacyIcon
} from '@heroicons/react/24/outline';
// Chart imports removed - using simple summary instead

const Dashboard = () => {
  const [pets, setPets] = useState([]);
  const [dashboardData, setDashboardData] = useState(null);
  const [loading, setLoading] = useState(true);
  const [showAddPetModal, setShowAddPetModal] = useState(false);

  const fetchPets = useCallback(async () => {
    try {
      const response = await axios.get('/api/pets');
      setPets(response.data.data);
      
      // Calculate dashboard stats directly from pet data
      setDashboardData(compileDashboardStats(response.data.data));
    } catch (error) {
      toast.error('Failed to fetch pets');
    } finally {
      setLoading(false);
    }
  }, []);

  useEffect(() => {
    fetchPets();
  }, [fetchPets]);

  const compileDashboardStats = (pets) => {
    const stats = {
      totalPets: pets.length,
      totalMedications: 0,
      upcomingAppointments: 0,
      recentReactions: 0
    };

    const today = new Date();
    const next30Days = new Date(today.getTime() + 30 * 24 * 60 * 60 * 1000);

    pets.forEach(pet => {
      // Count medications directly from pet data
      stats.totalMedications += pet.currentMedications?.length || 0;
      
      // Count upcoming appointments (next 30 days)
      if (pet.appointments) {
        const upcomingAppts = pet.appointments.filter(apt => {
          const aptDate = new Date(apt.date);
          return aptDate >= today && aptDate <= next30Days && apt.status === 'scheduled';
        });
        stats.upcomingAppointments += upcomingAppts.length;
      }
      
      // Count recent adverse reactions
      if (pet.adverseReactions) {
        const recentReactions = pet.adverseReactions.filter(reaction => {
          const reactionDate = new Date(reaction.date);
          const last30Days = new Date(today.getTime() - 30 * 24 * 60 * 60 * 1000);
          return reactionDate >= last30Days;
        });
        stats.recentReactions += recentReactions.length;
      }
    });

    return stats;
  };

  const getRiskColor = (riskLevel) => {
    switch (riskLevel) {
      case 'green': return 'text-green-600 bg-green-100';
      case 'yellow': return 'text-yellow-600 bg-yellow-100';
      case 'red': return 'text-red-600 bg-red-100';
      default: return 'text-gray-600 bg-gray-100';
    }
  };

  if (loading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  return (
    <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8 py-8">
      {/* Header */}
      <div className="flex justify-between items-center mb-8">
        <div>
          <h1 className="text-3xl font-bold text-gray-900">Pet Dashboard</h1>
          <p className="text-gray-600 mt-2">Monitor your pets' medication safety and upcoming appointments</p>
        </div>
        <button
          onClick={() => setShowAddPetModal(true)}
          className="btn-primary inline-flex items-center"
        >
          <PlusIcon className="h-5 w-5 mr-2" />
          Add Pet
        </button>
      </div>

      {pets.length === 0 ? (
        <div className="text-center py-12">
          <HeartIcon className="mx-auto h-12 w-12 text-gray-400" />
          <h3 className="mt-2 text-sm font-medium text-gray-900">No pets yet</h3>
          <p className="mt-1 text-sm text-gray-500">Get started by adding your first pet.</p>
          <div className="mt-6">
            <button
              onClick={() => setShowAddPetModal(true)}
              className="btn-primary inline-flex items-center"
            >
              <PlusIcon className="h-5 w-5 mr-2" />
              Add Pet
            </button>
          </div>
        </div>
      ) : (
        <>
          {/* Stats Overview */}
          {dashboardData && (
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6 mb-8">
              <div className="card">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <HeartIcon className="h-8 w-8 text-blue-600" />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Total Pets</p>
                    <p className="text-2xl font-semibold text-gray-900">{dashboardData.totalPets}</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <ClockIcon className="h-8 w-8 text-blue-600" />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Active Medications</p>
                    <p className="text-2xl font-semibold text-gray-900">{dashboardData.totalMedications}</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <CalendarDaysIcon className="h-8 w-8 text-yellow-600" />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Upcoming Appointments</p>
                    <p className="text-2xl font-semibold text-gray-900">{dashboardData.upcomingAppointments}</p>
                  </div>
                </div>
              </div>

              <div className="card">
                <div className="flex items-center">
                  <div className="flex-shrink-0">
                    <ExclamationTriangleIcon className="h-8 w-8 text-red-600" />
                  </div>
                  <div className="ml-4">
                    <p className="text-sm font-medium text-gray-500">Recent Reactions</p>
                    <p className="text-2xl font-semibold text-gray-900">{dashboardData.recentReactions}</p>
                  </div>
                </div>
              </div>
            </div>
          )}



          {/* Pet Cards */}
          <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
            {pets.map((pet) => {
              const upcomingAppts = pet.appointments?.filter(apt => {
                const aptDate = new Date(apt.date);
                const today = new Date();
                return aptDate >= today && apt.status === 'scheduled';
              }).length || 0;
              
              return (
                <div key={pet._id} className="card hover:shadow-lg transition-shadow duration-200">
                  <div className="flex items-center justify-between mb-4">
                    <div className="flex items-center">
                      <div className="w-12 h-12 bg-primary-100 rounded-full flex items-center justify-center">
                        <span className="text-2xl">
                          {pet.species === 'dog' ? '🐕' : pet.species === 'cat' ? '🐱' : '🐾'}
                        </span>
                      </div>
                      <div className="ml-3">
                        <h3 className="text-lg font-medium text-gray-900">{pet.name}</h3>
                        <p className="text-sm text-gray-500">{pet.species} • {pet.breed}</p>
                      </div>
                    </div>
                  </div>

                  <div className="space-y-2 mb-4">
                    <div className="flex justify-between text-sm">
                      <span className="text-gray-500">Active Medications:</span>
                      <span className="font-medium">{pet.currentMedications?.length || 0}</span>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span className="text-gray-500">Upcoming Appointments:</span>
                      <span className="font-medium">{upcomingAppts}</span>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span className="text-gray-500">Weight:</span>
                      <span className="font-medium">{pet.weight} {pet.weightUnit}</span>
                    </div>
                    <div className="flex justify-between text-sm">
                      <span className="text-gray-500">Age:</span>
                      <span className="font-medium">{pet.age} {pet.ageUnit}</span>
                    </div>
                  </div>

                  <div className="flex space-x-2">
                    <Link
                      to={`/pet/${pet._id}`}
                      className="btn-primary flex-1 text-center text-sm"
                    >
                      View Details
                    </Link>
                    <Link
                      to={`/api/reports/pet/${pet._id}/medication-history`}
                      className="btn-secondary p-2"
                      title="Download Report"
                    >
                      <DocumentArrowDownIcon className="h-4 w-4" />
                    </Link>
                  </div>
                </div>
              );
            })}
          </div>
        </>
      )}

      {/* Add Pet Modal */}
      {showAddPetModal && (
        <AddPetModal
          onClose={() => setShowAddPetModal(false)}
          onSuccess={() => {
            fetchPets();
            setShowAddPetModal(false);
          }}
        />
      )}
    </div>
  );
};

// Add Pet Modal Component
const AddPetModal = ({ onClose, onSuccess }) => {
  const [formData, setFormData] = useState({
    name: '',
    species: 'dog',
    breed: '',
    weight: '',
    weightUnit: 'kg',
    age: '',
    ageUnit: 'years',
    sex: 'male',
    neutered: false
  });

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
              await axios.post('/api/pets', {
        ...formData,
        weight: parseFloat(formData.weight),
        age: parseInt(formData.age)
      });
      toast.success('Pet added successfully!');
      onSuccess();
    } catch (error) {
      toast.error('Failed to add pet');
    }
  };

  return (
    <div className="fixed inset-0 bg-gray-600 bg-opacity-50 overflow-y-auto h-full w-full z-50">
      <div className="relative top-20 mx-auto p-5 border w-96 shadow-lg rounded-md bg-white">
        <h3 className="text-lg font-bold text-gray-900 mb-4">Add New Pet</h3>
        
        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="form-group">
            <label className="form-label">Pet Name</label>
            <input
              type="text"
              className="form-input"
              value={formData.name}
              onChange={(e) => setFormData({...formData, name: e.target.value})}
              required
            />
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="form-group">
              <label className="form-label">Species</label>
              <select
                className="form-input"
                value={formData.species}
                onChange={(e) => setFormData({...formData, species: e.target.value})}
              >
                <option value="dog">Dog</option>
                <option value="cat">Cat</option>
                <option value="bird">Bird</option>
                <option value="rabbit">Rabbit</option>
                <option value="other">Other</option>
              </select>
            </div>

            <div className="form-group">
              <label className="form-label">Breed</label>
              <input
                type="text"
                className="form-input"
                value={formData.breed}
                onChange={(e) => setFormData({...formData, breed: e.target.value})}
              />
            </div>
          </div>

          <div className="grid grid-cols-2 gap-4">
            <div className="form-group">
              <label className="form-label">Weight</label>
              <div className="flex">
                <input
                  type="number"
                  step="0.1"
                  className="form-input rounded-r-none"
                  value={formData.weight}
                  onChange={(e) => setFormData({...formData, weight: e.target.value})}
                  required
                />
                <select
                  className="form-input rounded-l-none border-l-0"
                  value={formData.weightUnit}
                  onChange={(e) => setFormData({...formData, weightUnit: e.target.value})}
                >
                  <option value="kg">kg</option>
                  <option value="lbs">lbs</option>
                </select>
              </div>
            </div>

            <div className="form-group">
              <label className="form-label">Age</label>
              <div className="flex">
                <input
                  type="number"
                  className="form-input rounded-r-none"
                  value={formData.age}
                  onChange={(e) => setFormData({...formData, age: e.target.value})}
                  required
                />
                <select
                  className="form-input rounded-l-none border-l-0"
                  value={formData.ageUnit}
                  onChange={(e) => setFormData({...formData, ageUnit: e.target.value})}
                >
                  <option value="months">months</option>
                  <option value="years">years</option>
                </select>
              </div>
            </div>
          </div>

          <div className="form-group">
            <label className="form-label">Sex</label>
            <select
              className="form-input"
              value={formData.sex}
              onChange={(e) => setFormData({...formData, sex: e.target.value})}
            >
              <option value="male">Male</option>
              <option value="female">Female</option>
            </select>
          </div>

          <div className="form-group">
            <label className="flex items-center">
              <input
                type="checkbox"
                className="rounded border-gray-300 text-blue-600 shadow-sm focus:border-blue-300 focus:ring focus:ring-blue-200 focus:ring-opacity-50"
                checked={formData.neutered}
                onChange={(e) => setFormData({...formData, neutered: e.target.checked})}
              />
              <span className="ml-2 text-sm text-gray-700">Neutered/Spayed</span>
            </label>
          </div>

          <div className="flex space-x-3 pt-4">
            <button type="submit" className="btn-primary flex-1">
              Add Pet
            </button>
            <button
              type="button"
              onClick={onClose}
              className="btn-secondary flex-1"
            >
              Cancel
            </button>
          </div>
        </form>
      </div>
    </div>
  );
};

export default Dashboard; 