import React from 'react';

const Footer = ({ className = '', fixed = false }) => {
  const footerClasses = fixed 
    ? "fixed bottom-0 left-0 right-0 bg-white border-t border-gray-200 py-4 z-10"
    : `bg-white border-t border-gray-200 py-4 mt-auto ${className}`;

  return (
    <footer className={footerClasses}>
      <div className="text-center">
        <p className="text-sm text-gray-500">Varun Bhat - 2025</p>
      </div>
    </footer>
  );
};

export default Footer; 